@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.primary-color {
    background-color: #01263D;
    font-family: 'Poppins', sans-serif !important;
}
.btn {
    color: white!important;
    min-width: 100%!important;
    border: 0px !important;
    line-height: 2.75!important;
    letter-spacing: 0.20857em!important;
    font-size: 1rem!important;
}

.paper-logo {
    width: 100%;
    background-color: transparent !important;
    height: 20vh;
}

.title {
    height: 5vh;
    margin: 10vh 2vh 2vh 2vh !important;
    border-radius: 10px; 
}

.generated-arrays {
    height: 60vh;
    border-radius: 10px; 
    margin: 5vh 2vh 2vh 2vh !important;
    position: relative
}

.bottom-controls {
    height: 5vh;
    margin: 5vh 2vh 2vh 2vh !important;
    border-radius: 10px; 
}

@media screen and (max-width : 1450px) {
    .title {
        margin: 10vh 1vh 1vh 1vh !important;
    }

    .first {
        margin: 10vh 1vh 1vh 3vh !important;
    }

    .bottom-first {
        margin: 5vh 1vh 1vh 3vh !important;
    }
}
.side-bar {
    background-color:'#01263D';
    color: 'white';
    font-family: 'Open Sans', cursive;
}
